body{
  font: 15px/1 "Hiragino Kaku Gothic Pro", "ヒラギノ角ゴ Pro W3", "メイリオ", "Meiryo", Helvetica, Arial, "ＭＳ Ｐゴシック", sans-serif;
}

.App{
  display: flex;
  align-items: center;
  justify-content: center;
}

.page{
  width: 1209px;
  height: 1720px;
  transform-origin: top left;
  top: 50%;
  left: 50%;
}
.page_content{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.page div, .page img{
  position: absolute;
}

.control_panel{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-delay: 1s;
  padding: 20px 0;
}

.control_panel.active{
  opacity: 1;
}

.control_panel:hover{
  opacity: 1;
  transition-delay: 0s;
}

.controls {
  padding: 7px 10px;
  border-radius: 4px;
  background: #444444;
  display: flex;
  align-items: center;
  justify-content: center;
}
.controls > * {
  margin: 0 10px;
}

.progress_bar{
  background: #444444;
  border: 1px solid #ffffff;
  height: 12px;
  width: 200px;
}
.progress_fill{
  background: #ffffff;
  height: 10px;
}