body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font: 15px/1 "Hiragino Kaku Gothic Pro", "\30D2\30E9\30AE\30CE\89D2\30B4   Pro W3", "\30E1\30A4\30EA\30AA", "Meiryo", Helvetica, Arial, "\FF2D\FF33   \FF30\30B4\30B7\30C3\30AF", sans-serif;
}

.App{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.page{
  width: 1209px;
  height: 1720px;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  top: 50%;
  left: 50%;
}
.page_content{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.page div, .page img{
  position: absolute;
}

.control_panel{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-delay: 1s;
  padding: 20px 0;
}

.control_panel.active{
  opacity: 1;
}

.control_panel:hover{
  opacity: 1;
  transition-delay: 0s;
}

.controls {
  padding: 7px 10px;
  border-radius: 4px;
  background: #444444;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.controls > * {
  margin: 0 10px;
}

.progress_bar{
  background: #444444;
  border: 1px solid #ffffff;
  height: 12px;
  width: 200px;
}
.progress_fill{
  background: #ffffff;
  height: 10px;
}
